@import '../../assets/styles/colors';
@import '../../assets/styles/mixins';

.artists-container {
  display: flex;
  flex-direction: column;

  .hints {
    display: flex;
    align-items: center;
    height: 40px;
    color: $gray;
    font-size: 13px;
    text-transform: uppercase;
  }

  .displayName {
    width: 30%;
  }

  .rating {
    width: 10%;
  }

  .email {
    width: 35%;
  }

  .createdAt {
    width: 25%;
    text-align: right;
  }

  .artist {
    display: flex;
    align-items: center;
    height: 60px;
    font-size: 20px;
    @include gradiend-border(bottom, 1px, (radial-gradient($black, $white) 1));

    .displayName {
      color: $dark-red;
      text-transform: uppercase;

      span {
        color: $gray;
        font-size: 13px;
      }
    }

    .rating {
      color: $rating;
      font-weight: bold;
    }
  }
}
